
@charset "UTF-8";
body, div, span, header, footer, nav, section, ul, li, a, p, h1, h2, h3, h4, h5, h6, i, textarea, input {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  font-weight: normal;
  font-family: "PingFangSC-Regular";
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: touch;
}
body:hover, div:hover, span:hover, header:hover, footer:hover, nav:hover, section:hover, ul:hover, li:hover, a:hover, p:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover, h6:hover, i:hover, textarea:hover, input:hover {
    outline: none;
}
*:not(input, textarea) {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  /*禁止苹果手机浏览器复制和粘贴功能，同时也导致文本框无法输入*/
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
html, body {
  background-color: #fff;
}
/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 0;
  height: 0;
  background-color: #F5F5F5;
}
/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px rgba(0, 0, 0, 0);
  border-radius: 10px;
  background-color: #F5F5F5;
}
/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input, textarea {
  outline: none;
  -webkit-appearance: none;
  border-radius: 0;
}
::-moz-placeholder {
  color: #cbcbcb;
}
::-webkit-input-placeholder {
  color: #cbcbcb;
}
:-ms-input-placeholder {
  color: #cbcbcb;
}
.mint-indicator-wrapper {
  z-index: 11;
}
.my-loadmore .mint-loadmore-top, .my-loadmore .mint-loadmore-bottom {
  height: 1.6rem;
  line-height: 1.6rem;
  font-size: .7rem;
}
.my-loadmore .mint-loadmore-top {
  margin-top: -1.6rem;
}
.my-loadmore .mint-loadmore-bottom {
  margin-bottom: -1.6rem;
}
.my-toast.mint-toast {
  min-width: 10em;
}
.my-switch .mint-switch-core {
  width: 40px;
  height: 20px;
}
.my-switch .mint-switch-core::after {
  width: 18px;
  height: 18px;
}
.my-switch .mint-switch-core::before {
  width: 38px;
  height: 18px;
}
.my-switch .mint-switch-input:checked + .mint-switch-core {
  border-color: #37c1f0;
  background-color: #37c1f0;
}
.my-arrow {
  position: relative;
  display: block;
  margin: 5px 8px 5px 0;
  height: 4px;
  background: #929292;
}
.my-arrow:after {
    position: absolute;
    content: '';
    right: -8px;
    top: -4px;
    width: 0;
    height: 0;
    border-width: 6px 0 6px  8px;
    border-color: transparent transparent transparent #929292;
    border-style: solid;
}
